<template>
  <div id="sub-header" class="row align-items-center">
    <AlloysSorting />
    <div class="pl-3">
      <div id="universChoice" class="row px-3">
        <span>
          <input
            id="1 - Aerospace high performance alloys"
            v-model="store.productUniverse"
            class="radio-button"
            type="radio"
            name="productUniverse"
            value="1 - Aerospace high performance alloys"
            @change="changeUniverse"
          />
          <label for="1 - Aerospace high performance alloys">
            Aerospace high
            <br />performance alloys
          </label>
        </span>
        <span>
          <input
            id="2 - Connectivity and free-machining alloys"
            v-model="store.productUniverse"
            class="radio-button"
            type="radio"
            name="productUniverse"
            value="2 - Connectivity and free-machining alloys"
            @change="changeUniverse"
          />
          <label for="2 - Connectivity and free-machining alloys">
            Connectivity and
            <br />free-machining alloys
          </label>
        </span>
      </div>
    </div>
    <div class="col-md-3 ml-auto">
      <b-input-group id="searchBar" size="lg" placeholder="Search">
        <b-input-group-text slot="append">
          <img class="search-button" src="@/assets/search.png" alt />
        </b-input-group-text>
        <b-form-input v-model="searchTerm" placeholder="Search"></b-form-input>
      </b-input-group>
    </div>
  </div>
</template>

<script>
import AlloysSorting from "@/components/AlloysSorting";
import Store from "@/store";

export default {
  components: {
    AlloysSorting
  },
  props: {
    clearSelection: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      store: Store.state,
      searchTerm: ""
    };
  },
  watch: {
    searchTerm: function(term) {
      this.search(term);
    }
  },
  methods: {
    changeUniverse(event) {
      Store.changeUniverse(event.target.value);
      this.clearSelection();
    },
    search() {
      Store.addFilter(null, "globalSearch", this.searchTerm);
    }
  }
};
</script>

