        { value: "tensileStrengthMPa", text: "Tensile Strength" },
        {
          value: "electricalConductivityIACS",
          text: "Electrical Conductivity"
        },
        { value: "density", text: "Density" }
      ]
    };
  },
  methods: {
    selectChange() {
      Store.sortAlloysItems(this.selected);
    }
  }
};
</script>

