<template>
  <div>
    <div class="btn-reset-ctn">
      <b-button class="filter-button btn-reset" variant="outline-*" @click="resetFilters">
        <img :src="getIcon('resetBtn.svg')" alt />
        <p class="filter-name">Reset filters</p>
      </b-button>
    </div>
    <div v-for="(filterGroup, key) in store.filtersList" :key="key" :title="key" active>
      <b-button v-b-toggle="key" class="filter-button" variant="outline-*">
        <img :src="getIcon(filterGroup.icon)" alt />
        <img class="filter-arrow" src="@/assets/filterArrow.png" alt />
        <p class="filter-name">{{ key }}</p>
      </b-button>
      <b-collapse :id="key">
        <div v-if="filterGroup.unit" class="unit-checkbox">
          <span class="switch">
            {{ filterGroup.unit[0].name }}
            <b-form-checkbox
              v-model="filterGroup.selectedUnit"
              name="check-button"
              switch
              class="unit-switch"
              @change="checkboxChange(filter)"
            ></b-form-checkbox>
            {{ filterGroup.unit[1].name }}
          </span>
        </div>
        <div class="section-filters">
          <div v-for="filter in filterGroup.datas" :key="filter.filterName" class="filter-item">
            <b-form-checkbox
              v-if="filter.type === 'checkbox'"
              v-model="filter.checked"
              @change="
                addFilter(filter.technicalId, filter.type, !filter.checked)
              "
            >{{ filter.filterName }}</b-form-checkbox>
            <div v-else-if="filter.type === 'slider'" class="slider">
              <div v-if="Array.isArray(filter.unit)">
                <div class="switch-header">
                  <span class="filter-name">{{ filter.filterName }}</span>
                  <span class="switch">
                    {{ filter.unit[0].name }}
                    <b-form-checkbox
                      v-model="filter.selectedUnit"
                      name="check-button"
                      switch
                      class="unit-switch"
                      @change="checkboxChange(filter)"
                    ></b-form-checkbox>
                    {{ filter.unit[1].name }}
                  </span>
                </div>
                <VueSlider
                  v-model="filter.selectedValue"
                  :min="filter.min"
                  :max="filter.max"
                  :interval="filter.interval"
                  :tooltip="'always'"
                  @drag-end="
                    addFilter(
                      filter.selectedUnit
                        ? filter.technicalId[1]
                        : filter.technicalId[0],
                      filter.type,
                      filter.selectedValue
                    )
                  "
                ></VueSlider>
              </div>
              <div v-else>
                <span class="filter-name">{{ filter.filterName }}</span>
                <VueSlider
                  v-model="filter.selectedValue"
                  :min="filter.min"
                  :max="filter.max"
                  :interval="filter.interval"
                  :tooltip="'always'"
                  @drag-end="
                    addFilter(
                      filter.technicalId,
                      filter.type,
                      filter.selectedValue
                    )
                  "
                ></VueSlider>
              </div>
            </div>
            <div v-else-if="filter.type === 'select'">
              <div class="row">
                <div class="col-5">{{ filter.filterName }}</div>
                <div class="col-7">
                  <b-form-checkbox-group
                    v-model="filter.value"
                    :options="[1, 2, 3, 4, 5]"
                    buttons
                    @change="
                      $nextTick(() => {
                        addFilter(
                          filter.technicalId,
                          filter.type,
                          filter.value
                        );
                      })
                    "
                  ></b-form-checkbox-group>
                </div>
              </div>
            </div>
            <div v-else-if="filter.type === 'text'">
              {{ filter.filterName }}
              <input
                v-model="filter.value"
                @keyup.enter="
                  addFilter(filter.technicalId, filter.type, filter.value)
                "
              />
            </div>
          </div>
        </div>
      </b-collapse>
    </div>
  </div>
</template>

<script>
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";
import Store from "@/store";

export default {
  components: {
    VueSlider
  },
  props: {
    alloys: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      store: Store.state,
      test: 1.1
    };
  },
  methods: {
    checkboxChange(filter) {
      /**
       * Si un filtre a été mis sur l'autre unité, on le supprime
       * Sachant que lorsqu'on clic on a l'état précédent de la checkbox.
       * Donc si on fait passer la checkbox à True, ici on a filter.selectedUnit à false
       */

      Store.resetSpecificFilter(
        filter.selectedUnit ? filter.technicalId[1] : filter.technicalId[0]
      );

      // Mise à jour des paramètres utilisés par le slider
      let unitMin = filter.selectedUnit
        ? filter.unit[0].valueMin
        : filter.unit[1].valueMin;
      let unitMax = filter.selectedUnit
        ? filter.unit[0].valueMax
        : filter.unit[1].valueMax;
      let unit = filter.selectedUnit
        ? filter.technicalId[0]
        : filter.technicalId[1];

      filter.min = unitMin;

      // Si la valeur courante est plus grande que la valeur max future
      if (filter.selectedValue > unitMax) {
        filter.selectedValue = (filter.selectedValue * 0.145038).toFixed(0);
        this.$nextTick(() => {
          filter.max = unitMax;
          this.addFilter(unit, filter.type, filter.selectedValue);
        });
      }
      // Si la valeur max future est plus grande que l'actuel max
      else if (unitMax > filter.max) {
        filter.max = unitMax;
        this.$nextTick(() => {
          filter.selectedValue = (filter.selectedValue / 0.145038).toFixed(0);
          this.addFilter(unit, filter.type, filter.selectedValue);
        });
      } else {
        filter.max = unitMax;
        this.addFilter(unit, filter.type, filter.selectedValue);
      }
    },
    getIcon(iconName) {
      return require("@/assets/" + iconName);
    },
    addFilter(property, type, value) {
      Store.addFilter(property, type, value);
    },
    resetFilters() {
      Store.resetFilters();
    }
  }
};
</script>

