<template>
  <div id="datavisualisation" class>
    <div class="header-section">
      <div class="tabs-selector-ctn">
        <b-button
          class="tab-selector"
          :class="{ active: tabName === 'datasheet' }"
          variant="outline-*"
          @click="tabName = 'datasheet'"
          >DATA SHEET</b-button
        >
        <b-button
          class="tab-selector"
          :class="{ active: tabName === 'graphic' }"
          variant="outline-*"
          @click="tabName = 'graphic'"
          >GRAPHIC OVERVIEW</b-button
        >
      </div>
      <div class="alloys-ctn">
        <AlloyItem
          v-for="(alloy, index) in selectedAlloys"
          :key="alloy.brandName + alloy.temper + alloy.isoDesignation"
          :alloy="alloy"
          class="itemAlloy"
          :is-selected="true"
          :is-active="isActive(alloy)"
          :color="graphicColors[index]"
          :alloy-from-visualization="true"
          @click.native="addActiveAlloy(alloy)"
        />
        <div
          v-for="n in 5 - selectedAlloys.length"
          :key="n"
          class="empty-alloy-item"
        >
          <div class="dashed"></div>
        </div>
      </div>
    </div>
    <div v-if="tabName === 'graphic'" class="graphic-wrapper">
      <Graphic :chart-data="datas" :options="options" />
      <div>
        <h6 class="my-3 text-uppercase">Couples de propriétés</h6>
        <!-- switch MPa/Ksi -->
        <div class="switch-header row mx-0 mb-3">
          <span>
            Ksi
            <b-form-checkbox
              v-model="selectedUnitBool"
              name="check-button"
              switch
              class="unit-switch"
              @change="selectUnit(selectedUnitBool)"
            ></b-form-checkbox
            >MPa
          </span>
        </div>
        <!-- couples -->
        <b-form-group>
          <b-form-radio
            v-for="(couple, index) in couplesList"
            :key="index"
            v-model="selected"
            name="some-radios"
            plain
            class="my-radio"
            :class="selected == index ? 'bold' : ''"
            :value="index"
          >
            {{
              couple.prop1.name +
                (couple.prop1.MPaKSI ? ` (${selectedUnit})` : "") +
                " / " +
                couple.prop2.name +
                (couple.prop2.MPaKSI ? ` (${selectedUnit})` : "")
            }}
          </b-form-radio>
        </b-form-group>
      </div>
    </div>
    <div v-else-if="tabName === 'datasheet'" id="datasheet-section">
      <img
        src="@/assets/arrow.png"
        alt
        class="d-none d-md-block arrowUp"
        @click="scroll('up')"
      />
      <DataSheet :alloys="selectedAlloys" class="dataSheet-wrapper" />
      <img
        src="@/assets/arrow.png"
        alt
        class="d-none d-md-block arrowDown"
        @click="scroll('down')"
      />
    </div>
  </div>
</template>

<script>
import Graphic from "@/components/DataVisualization/Graphic.vue";
import DataSheet from "@/components/DataVisualization/DataSheet.vue";
import Couples from "@/config/couples.js";
import AlloyItem from "@/components/AlloyItem";
import $ from "jquery";

export default {
  name: "DataVisualization",
  components: {
    Graphic,
    DataSheet,
    AlloyItem
  },
  props: {
    selectedAlloys: {
      type: Array,
      default: null
    }
  },
  data: () => {
    return {
      selected: 0,
      couplesList: JSON.parse(JSON.stringify(Couples)),
      tabName: "datasheet",
      selectedUnit: "MPa",
      selectedUnitBool: true,
      graphicColors: ["#ade4d9", "#85d6a8", "#43a19b", "#f98866", "#f75a2e"],
      activeAlloy: {}
    };
  },
  computed: {
    datas: function() {
      if (!this.selectedAlloys.length) {
        return {
          datasets: [{ label: "Aucun alliage sélectionné", data: [{}] }]
        };
      }
      let tmpDatasets = [];
      let { prop1, prop2 } = this.couplesList[this.selected];
      let property1 = prop1.MPaKSI
        ? prop1.property.concat("", this.selectedUnit)
        : prop1.property;
      let property2 = prop2.MPaKSI
        ? prop2.property.concat("", this.selectedUnit)
        : prop2.property;
      this.selectedAlloys.forEach((element, index) => {
        tmpDatasets.push({
          label: element.brandName,
          backgroundColor: this.graphicColors[index],
          data: [
            {
              x: this.processNumber(element[property1]),
              y: this.processNumber(element[property2]),
              r: 10
            }
          ]
        });
      });
      return {
        datasets: tmpDatasets
      };
    },
    options: function() {
      if (!this.selectedAlloys.length) {
        return {};
      }
      let { prop1, prop2 } = this.couplesList[this.selected];
      let property1 = prop1.MPaKSI
        ? prop1.property.concat("", this.selectedUnit)
        : prop1.property;
      let property2 = prop2.MPaKSI
        ? prop2.property.concat("", this.selectedUnit)
        : prop2.property;
      let prop1Datas = [],
        prop2Datas = [],
        tmpOptions = {};
      this.selectedAlloys.forEach(element => {
        prop1Datas.push(this.processNumber(element[property1]));
        prop2Datas.push(this.processNumber(element[property2]));
      });
      let interval1 = prop1.MPaKSI
        ? prop1["interval" + this.selectedUnit]
        : prop1.interval;
      let interval2 = prop2.MPaKSI
        ? prop2["interval" + this.selectedUnit]
        : prop2.interval;
      tmpOptions = {
        scales: {
          xAxes: [
            {
              ticks: {
                suggestedMin: Math.min(...prop1Datas) - interval1,
                suggestedMax: Math.max(...prop1Datas) + interval1
              },
              scaleLabel: {
                display: true,
                labelString:
                  prop1.name + (prop1.MPaKSI ? ` (${this.selectedUnit})` : "")
              }
            }
          ],
          yAxes: [
            {
              ticks: {
                suggestedMin: Math.min(...prop2Datas) - interval2,
                suggestedMax: Math.max(...prop2Datas) + interval2
              },
              scaleLabel: {
                display: true,
                labelString:
                  prop2.name + (prop2.MPaKSI ? ` (${this.selectedUnit})` : "")
              }
            }
          ]
        }
      };
      return tmpOptions;
    }
  },
  methods: {
    addSelectedAlloy(alloy) {
      this.$parent.addSelectedAlloy(alloy);
    },
    selectUnit: function(selection) {
      this.selectedUnit = selection ? "Ksi" : "MPa";
    },
    processNumber: function(stringValue) {
      if (!stringValue || stringValue == "-") return "";
      // if format of value is XXX-XXX
      if (/^[\d,]+[^\d,]+[\d,]+$/.test(stringValue))
        return Number.parseInt(stringValue);
      // to get rid of every non digit character except ','
      let digitString = stringValue.replace(/[^\d,]/g, "");
      return Number(digitString.replace(",", "."));
    },
    isActive(alloy) {
      if (
        this.activeAlloy &&
        this.activeAlloy.brandName === alloy.brandName &&
        this.activeAlloy.temper === alloy.temper
      )
        return true;
      return false;
    },
    addActiveAlloy(alloy) {
      if (
        this.activeAlloy.brandName === alloy.brandName &&
        this.activeAlloy.temper === alloy.temper
      )
        this.activeAlloy = {};
      else this.activeAlloy = alloy;
    },
    scroll(direction) {
      var upPos = $(".dataSheet-wrapper").scrollTop();
      let value = 0;
      if (direction === "up") {
        value = upPos - 200;
      } else {
        value = upPos + 200;
      }
      $(".dataSheet-wrapper").animate(
        {
          scrollTop: value
        },
        "fast"
      );
    }
  }
};
</script>

