<script>
import { Bubble, mixins } from "vue-chartjs";
const { reactiveProp } = mixins;

export default {
  extends: Bubble,
  mixins: [reactiveProp],
  props: {
    chartData: {
      type: Object,
      default: null
    },
    options: {
      type: Object,
      default: null
    }
  },
  watch: {
    options: {
      handler() {
        this.$data._chart.destroy();
        this.renderChart(this.chartData, this.options);
      },
      deep: true
    }
  },
  mounted() {
    this.renderChart(this.chartData, this.options);
  }
};
</script>
