<template>
  <div class="alloy-item">
    <div
      class="alloy-item-content"
      :class="{ selected: isSelected, active: isActive }"
      :style="{ backgroundColor: displayColor() }"
    >
      <div class="informations">
        <div class="main-informations">
          <p>{{ alloy.brandName }}</p>
        </div>
        <p>{{ alloy.temper }}</p>
        <div class="more-informations">
          <!-- TODO Définir les champs à afficher au survol -->
          <p>{{ alloy.isoDesignation }}</p>
          <p>{{ displayForms(alloy) }}</p>
        </div>
        <button type="button" @click="selectAlloy(alloy)">
          <img src="../assets/select-btn.png" alt="select" />
        </button>
      </div>
    </div>
    <div v-if="showCross()" class="cross-button" @click="selectAlloy(alloy)">
      &#10005;
    </div>
  </div>
</template>

<script>
export default {
  props: {
    alloy: {
      type: Object,
      required: true
    },
    isSelected: {
      type: Boolean
    },
    isActive: {
      type: Boolean
    },
    color: {
      type: String,
      required: false
    },
    alloyFromVisualization: {
      type: Boolean,
      required: false
    }
  },
  methods: {
    selectAlloy(alloy) {
      this.$parent.addSelectedAlloy(alloy);
    },
    displayForms: function(alloy) {
      let forms = [];
      if (alloy.rodForm) forms.push("Rod");
      if (alloy.wireForm) forms.push("Wire");
      if (alloy.stripForm) forms.push("Strip");
      return forms.join(", ");
    },
    displayColor: function() {
      return this.color
        ? this.color
        : `rgb(${this.alloy.red}, ${this.alloy.green}, ${this.alloy.blue})`;
    },
    showCross: function() {
      return this.isSelected && this.alloyFromVisualization;
    }
  }
};
</script>

