<template>
  <div class="tables-ctn">
    <!-- fields column -->
    <div class="fields-ctn">
      <table v-for="keyField in Object.keys(fields)" :key="keyField">
        <thead>
          <tr>
            <th class="text-left text-uppercase gradient-th">{{ keyField }}</th>
          </tr>
          <tr class="empty-row"></tr>
        </thead>
        <tbody>
          <tr v-for="field in fields[keyField]" :key="field.title">
            <td
              scope="row"
              :class="{ bigRow: isKeyNorms(field.title) }"
              class="text-left"
              v-html="field.name"
            ></td>
          </tr>
        </tbody>
      </table>
    </div>
    <!-- alloys infos -->
    <div class="alloy-cols-ctn">
      <!-- alloy column -->
      <div
        v-for="(alloy, index) in alloys"
        :key="index + '_' + alloy.isoDesignation"
        class="alloy-col"
      >
        <!-- group table -->
        <table
          v-for="keyField in Object.keys(fields)"
          :key="keyField + '-' + alloy.isoDesignation"
        >
          <thead>
            <tr>
              <th>&nbsp;</th>
            </tr>
            <tr class="empty-row"></tr>
          </thead>
          <tbody>
            <tr
              v-for="field in fields[keyField]"
              :key="alloy.isoDesignation + '-' + field.title"
            >
              <td
                scope="row"
                :class="{ bigRow: isKeyNorms(field.title) }"
                v-html="displayValue(field.title, alloy[field.title])"
              ></td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- empty cols -->
      <div
        v-for="k in 5 - alloys.length"
        :key="k + '_column'"
        class="alloy-col"
      >
        <!-- group table -->
        <table
          v-for="keyField in Object.keys(fields)"
          :key="k + '-' + keyField"
        >
          <thead>
            <tr>
              <th>&nbsp;</th>
            </tr>
            <tr class="empty-row"></tr>
          </thead>
          <tbody>
            <tr v-for="field in fields[keyField]" :key="k + '-' + field.title">
              <td scope="row" :class="{ bigRow: isKeyNorms(field.title) }">
                —
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Fields from "@/config/fieldsDataSheet.js";

export default {
  name: "DataSheet",
  props: {
    alloys: {
      type: Array,
      default: null
    }
  },
  data: () => {
    return {
      fields: JSON.parse(JSON.stringify(Fields))
    };
  },
  watch: {
    alloys: function(oldAlloys, newAlloys) {
      return newAlloys;
    }
  },
  methods: {
    displayValue: function(title, value) {
      if (!value) return "—";
      if (title == "productUniverse") return value.split(" ", 3).join(" ");
      if (typeof value == "boolean") return value ? "&#11044;	" : "-";
      if (title == "NACECorrosion" || title == "friction") {
        let i;
        let stars = "";
        for (i = 0; i < 5; i++) {
          let div =
            i < value
              ? `<img src="${require("@/assets/icon-star.png")}" style="opacity:0.8;" alt>`
              : `<img src="${require("@/assets/icon-star.png")}" style="opacity:0.5;" alt>`;
          stars += div;
        }
        return stars;
      }
      return value;
    },
    isKeyNorms: function(title) {
      return title == "keyNorms" ? true : false;
    }
  }
};
</script>

